<template>
	<div class="navigation">
		<div id="mobile-menu-open" class="shadow-large" @click="onOpenMobileMenu">
			<i class="fa fa-bars" aria-hidden="true"></i>
		</div>
		<!-- End #mobile-menu-toggle -->
		<header v-if="isMenuVisible">
			<div id="mobile-menu-close" @click="onCloseMobileMenu">
				<span>Close</span>
				<i class="fa fa-times" aria-hidden="true"></i>
			</div>
			<ul id="menu" class="shadow">
				<li>
					<a title="about" @onClick="onClickNavigation('about')">About</a>
				</li>
				<li>
					<a title="experience" @onClick="onClickNavigation('experience')">Experience</a>
				</li>
				<li>
					<a @onClick="onClickNavigation('eduction')" title="education">Education</a>
				</li>
				<li>
					<a @onClick="onClickNavigation('projects')" title="projects">Projects</a>
				</li>
				<li>
					<a @onClick="onClickNavigation('articles')" title="articles">Articles</a>
				</li>
				<li>
					<a @onClick="onClickNavigation('skills')" title="skills">Skills</a>
				</li>
				<!-- <li>
          <a href="https://vanwildemeerschbrent.github.io/blog/" title="blog"
            >Blog</a
          >
				</li>-->
				<li>
					<a href="#contact" title="contact">Contact</a>
				</li>
			</ul>
		</header>
	</div>
</template>

<script>
	let self = null;
	export default {
		name: "navigation",

		data() {
			return {
				isMenuVisible: document.getElementsByTagName("body")[0].clientWidth > 770
			};
		},

		created() {
			this.initResizeObserver();
			self = this;
		},

		methods: {
			onClickNavigation(id) {
				this.isMenuVisible = false;
				document.getElementById(id)[0].scrollIntoView({
					behavior: "smooth"
				});
			},
			onOpenMobileMenu() {
				this.isMenuVisible = true;
			},

			onCloseMobileMenu() {
				this.isMenuVisible = false;
			},

			initResizeObserver() {
				const resizeObserver = new ResizeObserver(() => {
					self.isMenuVisible =
						document.getElementsByTagName("body")[0].clientWidth > 770;
				});

				resizeObserver.observe(document.body);
			}
		}
	};
</script>

<style lang="scss" scoped>
</style>